'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useAuth } from '@/context/AuthContext';
import api from '@/lib/api';

export default function HomePage() {
  const { user } = useAuth();
  const [universities, setUniversities] = useState([]);
  const [hostels, setHostels] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [selectedUniversity, setSelectedUniversity] = useState('');

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [univRes, hostelRes] = await Promise.all([
        api.get('/hostels/universities'),
        api.get('/hostels/approved')
      ]);
      setUniversities(univRes.data.universities);
      setHostels(hostelRes.data.hostels);
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setLoading(false);
    }
  };

  const filteredHostels = hostels.filter(hostel => {
    const matchesSearch = hostel.name.toLowerCase().includes(search.toLowerCase()) ||
      hostel.city.toLowerCase().includes(search.toLowerCase());
    const matchesUniversity = selectedUniversity
      ? hostel.university_id === selectedUniversity
      : true;
    return matchesSearch && matchesUniversity;
  });

  return (
    <div className="min-h-screen bg-gray-50">

      {/* Navbar */}
      <nav className="bg-white shadow-sm sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 py-4 flex justify-between items-center">
          <Link href="/" className="text-2xl font-bold text-blue-600">
            DormLink
          </Link>
          <div className="flex gap-4 items-center">
            {user ? (
              <Link
                href={`/${user.role}/dashboard`}
                className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
              >
                Dashboard
              </Link>
            ) : (
              <>
                <Link href="/login" className="text-gray-600 hover:text-blue-600">
                  Login
                </Link>
                <Link
                  href="/register"
                  className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
                >
                  Get Started
                </Link>
              </>
            )}
          </div>
        </div>
      </nav>

      {/* Hero Section */}
      <div className="bg-blue-600 text-white py-20 px-4">
        <div className="max-w-4xl mx-auto text-center">
          <h1 className="text-5xl font-bold mb-6">
            Find Your Perfect Student Home
          </h1>
          <p className="text-xl mb-10 text-blue-100">
            Verified accommodation near top Tanzanian universities.
            Safe, affordable, and just a booking away.
          </p>

          <div className="bg-white rounded-2xl p-4 flex flex-col md:flex-row gap-3">
            <input
              type="text"
              placeholder="Search by hostel name or city..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="flex-1 px-4 py-3 text-gray-800 rounded-xl border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
            <select
              value={selectedUniversity}
              onChange={(e) => setSelectedUniversity(e.target.value)}
              className="px-4 py-3 text-gray-800 rounded-xl border border-gray-200 focus:outline-none"
            >
              <option value="">All Universities</option>
              {universities.map(u => (
                <option key={u.id} value={u.id}>{u.name}</option>
              ))}
            </select>
            <button className="bg-blue-600 text-white px-8 py-3 rounded-xl font-semibold hover:bg-blue-700">
              Search
            </button>
          </div>
        </div>
      </div>

      {/* Stats */}
      <div className="bg-white py-12">
        <div className="max-w-7xl mx-auto px-4 grid grid-cols-3 gap-8 text-center">
          <div>
            <p className="text-4xl font-bold text-blue-600">{hostels.length}+</p>
            <p className="text-gray-500 mt-1">Verified Hostels</p>
          </div>
          <div>
            <p className="text-4xl font-bold text-blue-600">{universities.length}</p>
            <p className="text-gray-500 mt-1">Universities Covered</p>
          </div>
          <div>
            <p className="text-4xl font-bold text-blue-600">100%</p>
            <p className="text-gray-500 mt-1">Verified Listings</p>
          </div>
        </div>
      </div>

      {/* Hostels Grid */}
      <div className="max-w-7xl mx-auto px-4 py-16">
        <h2 className="text-3xl font-bold text-gray-800 mb-8">
          Available Accommodation
        </h2>

        {loading ? (
          <div className="text-center py-20 text-gray-400">Loading hostels...</div>
        ) : filteredHostels.length === 0 ? (
          <div className="text-center py-20">
            <p className="text-gray-400 text-xl">No hostels found yet.</p>
            <p className="text-gray-400 mt-2">Check back soon!</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredHostels.map(hostel => (
              <Link key={hostel.id} href={`/hostels/${hostel.id}`}>
                <div className="bg-white rounded-2xl shadow-sm hover:shadow-md transition-shadow overflow-hidden cursor-pointer">
                  <div className="bg-blue-100 h-48 flex items-center justify-center">
                    <span className="text-6xl">üè†</span>
                  </div>
                  <div className="p-5">
                    <h3 className="text-xl font-bold text-gray-800">{hostel.name}</h3>
                    <p className="text-gray-500 text-sm mt-1">
                      {hostel.address}, {hostel.city}
                    </p>
                    {hostel.universities && (
                      <p className="text-blue-600 text-sm mt-1">
                        {hostel.universities.name}
                      </p>
                    )}
                    {hostel.amenities && hostel.amenities.length > 0 && (
                      <div className="flex flex-wrap gap-2 mt-3">
                        {hostel.amenities.slice(0, 3).map((a, i) => (
                          <span key={i} className="bg-blue-50 text-blue-600 text-xs px-2 py-1 rounded-full">
                            {a}
                          </span>
                        ))}
                      </div>
                    )}
                    {hostel.rooms && hostel.rooms.length > 0 && (
                      <p className="text-green-600 font-bold mt-3">
                        From TZS {Math.min(...hostel.rooms.map(r => r.price_per_month)).toLocaleString()}/month
                      </p>
                    )}
                    <button className="w-full mt-4 bg-blue-600 text-white py-2 rounded-xl hover:bg-blue-700">
                      View Details
                    </button>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        )}
      </div>

      {/* Footer */}
      <footer className="bg-gray-800 text-white py-12 px-4">
        <div className="max-w-7xl mx-auto grid grid-cols-1 md:grid-cols-3 gap-8">
          <div>
            <h3 className="text-xl font-bold mb-4">DormLink</h3>
            <p className="text-gray-400">Tanzania trusted student accommodation platform.</p>
          </div>
          <div>
            <h4 className="font-bold mb-4">Quick Links</h4>
            <ul className="space-y-2 text-gray-400">
              <li><Link href="/login" className="hover:text-white">Login</Link></li>
              <li><Link href="/register" className="hover:text-white">Register</Link></li>
            </ul>
          </div>
          <div>
            <h4 className="font-bold mb-4">Contact</h4>
            <p className="text-gray-400">support@dormlink.co.tz</p>
          </div>
        </div>
        <div className="text-center text-gray-500 mt-8">
          2024 DormLink. All rights reserved.
        </div>
      </footer>
    </div>
  );
}